@import url("https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@500&family=Patrick+Hand&family=Titillium+Web:wght@300&display=swap");
.about-page-container {
  font-family: 'Patrick Hand', cursive;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 2fr 2fr;
      grid-template-columns: 2fr 2fr;
  -ms-grid-rows: 1fr;
      grid-template-rows: 1fr;
  height: 100%;
  padding: 20px 20px;
  margin-top: 20px;
  background-image: url("../../media/images/VectorBottom.svg"), url("../../media/images/VectorTop.svg");
  background-repeat: no-repeat;
  background-position: bottom left, top right;
  background-size: 50%, 30%;
  border-radius: 20px 20px 0px 0px;
  -webkit-box-shadow: 2px 2px 20px 2px #a3a1a1;
          box-shadow: 2px 2px 20px 2px #a3a1a1;
  min-height: 85vh;
}

@media (max-width: 1117px) {
  .about-page-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.about-page-container .about-profile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  margin-top: 20px;
  height: 100%;
  border-radius: 20px;
}

.about-page-container .about-profile .profile-picture-container {
  width: 350px;
  height: 500px;
  margin: 20px;
  border-radius: 50%;
}

@media (max-width: 668px) {
  .about-page-container .about-profile .profile-picture-container {
    width: 300px;
    height: 400px;
  }
}

@media (max-width: 450px) {
  .about-page-container .about-profile .profile-picture-container {
    width: 250px;
    height: 370px;
  }
}

.about-page-container .about-profile .profile-picture-container .profile-picture {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.about-page-container .about-profile .action-button {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  bottom: 60px;
  left: 50px;
  background-color: #96bb96;
  padding: 20px;
  color: white;
  text-decoration: none;
  font-size: 22px;
  border-radius: 50px;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}

@media (max-width: 1117px) {
  .about-page-container .about-profile .action-button {
    position: absolute;
    bottom: -60px;
    left: auto;
  }
}

@media (max-width: 350px) {
  .about-page-container .about-profile .action-button {
    position: absolute;
    bottom: -90px;
    text-align: center;
    left: auto;
  }
}

.about-page-container .about-profile .action-button:hover, .about-page-container .about-profile .action-button:focus {
  cursor: pointer;
  background-color: #7D8779;
  -webkit-box-shadow: inset -3.5em 0 0 0 var(--hover), inset 3.5em 0 0 0 var(--hover);
          box-shadow: inset -3.5em 0 0 0 var(--hover), inset 3.5em 0 0 0 var(--hover);
}

.about-page-container .profile-main-text {
  margin-top: 40px;
  margin-right: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.about-page-container .profile-main-text q {
  font-size: 42px;
  margin-bottom: 15px;
  margin-top: 120px;
  text-align: center;
  color: #DECAD5;
}

.about-page-container .profile-main-text p {
  font-size: 20px;
  padding: 20px;
  padding-bottom: 50px;
}
