@mixin navColor { 
    background-color: #7D8779; 
}

@mixin pageColor { 
    background: rgb(247,250,245);
    background: linear-gradient(180deg, rgba(247,250,245,1) 0%, rgba(234,255,233,1) 100%); 
}

@mixin vectorColor { 
    background-color: #DECAD5; 
}

@mixin accentColor { 
    color: #DECAD5; 
}

@mixin mainButtonColor { 
    background-color: rgb(150, 187, 150);
}

@mixin focusColor { 
    border: 2px solid rgb(150, 187, 150);
    outline: none;
}

@mixin linkColor { 
    color: rgb(150, 187, 150);
}

@mixin linkHoverColor { 
    color: rgb(179, 236, 179);
}